@import 'styles/theme.sass';

.fancy-link {
    display: inline-block;
    height: 20px;
    padding: 4px 18px 22px;
    margin-right: 8px;
    margin-bottom: 14px;
    color: $fontColor;
    fill: $fontColor;
    text-decoration: none;
    background-color: $colorPrimary;
    border-radius: 4px;
    box-shadow: 0 6px 1px $colorBoxShadow;
    &:hover {
        background-color: $colorPrimaryDarker;
        box-shadow: 0 4px 1px $colorBoxShadow;
        transform: translateY(2px); }
    &:active {
        box-shadow: 0 2px 1px $colorBoxShadow;
        transform: translateY(4px); } }




//=========== specials ===============

.fancy-link-icon {
    height: 18px;
    margin-right: 4px;
    vertical-align: text-top; }

