.fancy-link {
  display: inline-block;
  height: 20px;
  padding: 4px 18px 22px;
  margin-right: 8px;
  margin-bottom: 14px;
  color: white;
  fill: white;
  text-decoration: none;
  background-color: #1E90FF;
  border-radius: 4px;
  box-shadow: 0 6px 1px #125699; }
  .fancy-link:hover {
    background-color: #1b81e5;
    box-shadow: 0 4px 1px #125699;
    -webkit-transform: translateY(2px);
            transform: translateY(2px); }
  .fancy-link:active {
    box-shadow: 0 2px 1px #125699;
    -webkit-transform: translateY(4px);
            transform: translateY(4px); }

.fancy-link-icon {
  height: 18px;
  margin-right: 4px;
  vertical-align: text-top; }

.fancy-link-github {
  background-color: #6e5494;
  box-shadow: 0 6px 1px #423258; }
